<template>
    <div class="collection">
        <div class="grid">
            <div class="gutter-sizer"></div>
            <div class="grid-sizer"></div>

            <div class="row">

                <div v-for="collection in collections" class="column">
                    <div class="grid-item" style="width: 80%;">
                        <img class="img-responsive" :src="collection?.thumbnail" style="width:100%">
                        <a :href="'/collection/' + collection.id" class="project-description">
                            <div class="project-text-holder">
                                <div class="project-text-inner">
                                    <h3>{{ collection?.name }}</h3>
                                    <p>{{ collection?.description }}</p>
                                </div>
                            </div>
                        </a>
                    </div>
                </div>
                

            </div>

        </div>
    </div>
</template>

<script>
import i18n from '@/i18n/i18n';

export default {
    name: 'Collection',
    data() {
        return {
            // Your data properties here
            collections: []
        };
    },
    methods: {
        // Your methods here
        fetchCollections(locale) {
            // Implement your fetch services logic here
            const collectionsGet = require(`@/assets/data/collection_international.json`);
            const allInternationaleCollections = collectionsGet.data;

            // create a new array to store the collections by following pattern
            // {
            //     "name": ...
            //     "description": ...
            //     "thumbnail": ...
            // }

            const returnCollections = [];
            for (const collection of allInternationaleCollections) {
                console.log(collection);
                // first searching in translation
                collection.translation.forEach(translation => {
                    if (translation.locale == locale) {
                        returnCollections.push({
                            id : collection.id,
                            name: translation.name || collection.name,
                            description: translation.description || collection.description,
                            thumbnail: collection.thumbnail
                        });
                    }
                });
            }

            // process data by finding 
            return returnCollections;
        },
        getThumbnailPath(thumbnail) {
            return thumbnail ? `/assets/images/${thumbnail}` : '';
        }
    },
    mounted() {
        // Your mounted hook logic here
        this.collections = this.fetchCollections(i18n.global.locale);

        // Implement your logic here
        console.log(this.collections);

        this.$watch(() => i18n.global.locale, (newLocale, oldLocale) => {
            this.collections = this.fetchCollections(newLocale);
        });
    },
};
</script>

